import Vue from 'vue'
import Router from 'vue-router'

const IndexPage = resolve => require(['@/pages/index.vue'], resolve)
const OrderListPage = resolve => require(['@/pages/orderList.vue'], resolve)
const DetailPage = resolve => require(['@/pages/detail.vue'], resolve)
const DetailCouPage = resolve => require(['@/pages/detail/count.vue'], resolve)
const DetailForPage = resolve => require(['@/pages/detail/forecast.vue'], resolve)
const DetailAnaPage = resolve => require(['@/pages/detail/analysis.vue'], resolve)
const DetailPubPage = resolve => require(['@/pages/detail/publish.vue'], resolve)
Vue.use(Router)

export default new Router({
	mode: 'history',
  routes: [
    {
      path: '/',
      name: 'index',
      component: IndexPage
    },
    {
      path: '/orderlist',
      name: 'orderList',
      component: OrderListPage
    },
    {
      path: '/detail',
      name: 'detail',
      redirect: '/detail/count',
      component: DetailPage,
      children: [
      	{
      		path: 'count',
      		name: 'detailCount',
      		component: DetailCouPage
      	},
      	{
      		path: 'forecast',
      		name: 'detailForecast',
      		component: DetailForPage
      	},
      	{
      		path: 'analysis',
      		name: 'detailAnalysis',
      		component: DetailAnaPage
      	},
      	{
      		path: 'publish',
      		name: 'detailPublish',
      		component: DetailPubPage
      	}
      ]
    }
  ]
})



