      		<router-view></router-view>
      	</keep-alive>
     	</transition>
    </div>
    <div class="app-foot">
      <p>© 2017 fanerge by github </p>
    </div>
    <mydialog :isShow="isShowLogDialog" @no-close="closeDialog('isShowLogDialog')">
    	<log-form></log-form>
    </mydialog>
    <mydialog :isShow="isShowAboutDialog" @no-close="closeDialog('isShowAboutDialog')">
    	<p>个人项目个人项目个人项目个人项目个人项目个人项目个人项目个人项目个人项目个人项目个人项目个人项目个人项目个人项目个人项目个人项目个人项目个人项目个人项目个人项目个人项目个人项目个人项目个人项目个人项目个人项目个人项目个人项目</p>
    </mydialog>
  </div>
</template>

<script>
import Mydialog from '@/components/dialog'
import LogForm from '@/components/logform'
export default {
  name: 'app',
  data () {
  	return {
      isShowLogDialog: false,
      isShowRegDialog: false,
      isShowAboutDialog: false
  	}
  },
  methods: {
  	logClick () {
      this.isShowLogDialog = true;
    },
    aboutClick () {
    	this.isShowAboutDialog = true
    },
    closeDialog (attr) {
      this[attr] = false
    },
  },
  components: {
  	Mydialog,
  	LogForm
  }
}
</script>

<style>
	.fade-enter-active, .fade-leave-active {
	  transition: opacity .5s
	}
	.fade-enter, .fade-leave-to /* .fade-leave-active in <2.1.8 */ {
	  opacity: 0
	}
	.app-head {
	  background: #363636;
	  color: #b2b2b2;
	  height: 90px;
	  line-height: 90px;
	  width: 100%;
	}
	.app-head-inner {
	  width: 1200px;
	  margin: 0 auto;
	}
	.head-logo {
	  float: left;
	  margin-top: 20px;
	}
	.head-logo img {
